import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  loading: false,
  accounts_data: [],
  catalogue_data:[],
  factory_list: [],
  brokersr_list: [],
  rating_list: [],
  catalogue_list: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;

export const getAllfactory = createAsyncThunk("auction/getfactory", async () => {
  const response = await axios.get(`${API_URL}/getfactory`);
  return response.data;
  
});
export const getAllrating = createAsyncThunk("auction/getrating", async () => {
  const response = await axios.get(`${API_URL}/getrating`);
  return response.data;
  
});
export const getAllbrokers = createAsyncThunk("auction/getallbrokers", async () => {
  const response = await axios.get(`${API_URL}/getallbrokers`);
  return response.data;
  
});
export const getAllcatalogue = createAsyncThunk("auction/getallcatalogue", async () => {
  const response = await axios.get(`${API_URL}/catalouge`);
  return response.data;
  
});

export const getAllwarehouse = createAsyncThunk("auction/getgarden", async () => {
  const response = await axios.get(`${API_URL}/getgarden`);
  return response.data;
});




export const saveData = createAsyncThunk("auction/createcatalogue", async (accounts) => {
  const response = await axios.post(`${API_URL}/createcatalogue`, {
    adata: accounts
  });
  return response.data;
 
});

export const updateData = createAsyncThunk("auction/updatedata", async (accounts) => {
//  console.log(lcpCart);
  const response = await axios.put(`${API_URL}/updatecataloguedata`, {
    adata: accounts,
  });
  return response.data;
});

export const selectData = createAsyncThunk("auction/selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectcatalogue`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("auction/deletedata", async (props) => {
 
    const response =axios.put(`${API_URL}/deletecatalogue`,{
      id: props,
    })
    return response.data;
      
    });
 
    
const catalogueSlice = createSlice({
  name: "catalogue",
  initialState,
  extraReducers: {
    [getAllcatalogue.fulfilled]: (state,action) => {
      const { payload } = action;
      state.catalogue_list=payload;

      // state.edit=true;
    },
    [getAllbrokers.fulfilled]: (state,action) => {
      const { payload } = action;
      state.brokersr_list = payload;
      // state.edit=true;
    },
    [getAllfactory.fulfilled]: (state,action) => {
      const { payload } = action;
      state.factory_list = payload;
      // state.edit=true;
    },
    [getAllrating.fulfilled]: (state,action) => {
      const { payload } = action;
      state.rating_list = payload;
      // state.edit=true;
    },
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
   
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state) => {
      state.edit=false;
      
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.catalogue_data = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {} = catalogueSlice.actions;
const catalogueReducer = catalogueSlice.reducer;

export default catalogueReducer;
