import React from "react";
    // const storedValue = localStorage.getItem("isVisible");
    // const str = JSON.parse(storedValue);
    // console.log(typeof(str));
let tableCount = 1;

function generateCategoryTable(data,user) {

  const categories = [...new Set(data.map(item => item.factory_name))];
  const invoices = [...new Set(data.map(item => item.factory_name))];

  let tableHTML = '';
tableHTML=` <html>
<head>
  <meta charset="UTF-8">
  <style>
  table {
    border-collapse: collapse;
   
  }
  
  td {
    width: 150px;
    padding: 5px;
    border: none;
    text-align: center;
    font-size: 16px;
    color: black;
  }
  
    .table-wrapper {
        margin: 0 auto;
        width: 900px;
        padding: 20px;
        background-color: #ffffff;
        
    
    }
    td.top-border {
      border-top: 2px solid black;
    }
    
    td.bottom-lines {
      position: relative;
    }
    
    td.bottom-lines::after {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      border-bottom: 1px solid black;
    }
    
    td.bottom-lines::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      border-bottom: 1px solid black;
    }
    
    
    
    #t-header .h1{
      padding:0px;
    }
    .brokers-n{
      padding:0px;
      text-align:center;
    }
    .season{
      text-align:center;

    }
    .tbl-data{
      display: flex;
  justify-content: center;
  align-items: center;
    }
    .factory-name{
      text-align:center;
    }
    
    body{
      font-family: 'Courier New', Courier, monospace;
      color: #000;
background: #ededed;
font-size: 12px;
    }

    @media print {
        body * {
          visibility: hidden;
        }
        .table-wrapper, .table-wrapper * {
          visibility: visible;
        }

        .header-print {
            position: fixed;
            top: 0;
            left: 0;
            padding: 10px;
            font-size: 24px;
            font-weight: bold;
            text-align: center;
            width: 100%;
          }
      
      }

  </style>
</head>
<body>
  <div class="print-btn" style="position: fixed; top: 0; left: 0; padding: 10px; background-color: white;">
    <button id="print-button" onclick="window.print()">Print</button>
    <button onclick="window.close()">Close</button>
  </div>
  
  <div class="table-wrapper">
           
  <div id="t-header" class="table-header">
  <h1 class="brokers-n">${user}</h1>
  <p class ="season">SEASON 2023/24</p>
  <hr style="border-top: 1px dotted #ccc;">
</div>
</div>
  `
   categories.forEach(category => {
    invoices.forEach(invoice => {
      const categoryData = data.filter(item => item.factory_name === category && item.factory_name === invoice);

      if (categoryData.length > 0) {
        let totalSales = 0;
        let totalPkgs = 0;
let totalinvSales = 0;
        tableHTML += `
         
              
              <div class="table-wrapper">
           
                <h2 class="factory-name">FACTORY: ${category} </h2>
                <div class="tbl-data">
                <table>
                  <thead>
                    <tr>
                      <th>Sl</th>
                      <th>Lot</th>
                      <th>Grade</th>
                      <th>Weight/bag</th>
                      <th>Pkg</th>
                      <th>Total</th>
                    </tr>
                  </thead>
                  <tbody>
        `;

        categoryData.forEach((item, index) => {
          tableHTML += `
            <tr>
              <td>${index + 1}</td>
              <td>${item.lot}</td>
              <td>${item.grade}</td>
              <td>${item.net_weight}</td>
              <td>${item.pkgs}</td>
              <td>${item.gross_total}</td>
            
            </tr>
          `;

          totalSales += Number(item.gross_total);
          totalinvSales += Number(item.totalkg);
          totalPkgs += Number(item.pkgs);
        });

        tableHTML += `
                  </tbody>
                  <tfoot>
                    <tr>
                    <th>TOT .INV KG ${totalinvSales}</th>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td class="top-border bottom-lines">${totalPkgs} Pkg</td>
                      <td class="top-border bottom-lines">${totalSales} Kg</td>
                    </tr>
                  </tfoot>
                </table>
                </div>
                <caption> ${tableCount}</caption>
              </div>
              <script>
                function printTable() {
                  window.print();
                }
              </script>
            </body>
          </html>
        `;

        tableCount++;
      }
    });
  });

  const newTab = window.open('', '_blank');
  newTab.document.body.innerHTML = tableHTML;


}

const OpenHTMLButton = ({ data,user }) => {
  const handleClick = () => {
    generateCategoryTable(data,user)
  };

  return <button className="btn btn-primary" onClick={handleClick}>Catalouge</button>;
};

export default OpenHTMLButton;
