import "./App.css";
import SideMenu from "./components/SideMenu";
import React, { useState, lazy, Suspense } from "react";
import { BrowserRouter as Router, Route, Routes,useLocation } from "react-router-dom";
import ReportAll from "./pages/ReportAll";
const Dashboard = lazy(() => import("./pages/Dashboard"));
const Login = lazy(() => import("./pages/login/Login"));
const Catalogue = lazy(() => import("./pages/Catalogue"));
const Bidders = lazy(() => import("./pages/Bidders"));
const Auction = lazy(() => import("./pages/Auction"));
const Association = lazy(() => import("./pages/auction/Association"));
const DemoCard = lazy(() => import("./pages/auction/DemoCard"));
// const API_URL = process.env.REACT_APP_SERVER_URL;


const App = () => {
  const location = useLocation();
  const showSidebar = location.pathname !== '/';
  const [inactive, setInactive] = useState(true);
  return (
    <div className="">
         {showSidebar &&    <SideMenu
          onCollapse={(inactive) => {
            setInactive(inactive);
          }}
        />}
     

        <div className={` ${showSidebar ? (inactive?"container2 inactive":"container2") : "sidebars"}`} >
        <Suspense fallback={<div>Loading...</div>}>
       
            <Routes>
            <Route exact path="/" element={<Login />} />
            <Route path="/association"  element={
               <Association />
            } />
              <Route path="/democard"  element={
               <DemoCard />
            } />
            <Route path="/dashboard"  element={
               <Dashboard />
            } />
            <Route path="/catalogue"  element={
               <Catalogue />
            } />
            <Route path="/bidders" element={
             <Bidders />
            } />
            <Route path="/auction"  element={
             <Auction />
            } />
             <Route path="/reportall"  element={
             <ReportAll />
            } />
            </Routes>
         
        </Suspense>
      </div>

      {showSidebar? (
        <>
        <div className={`main-footer ${inactive  ? "m-inactive" : ""}`}>
          <strong>
            Copyright © 2020-2022
            <a href="">It Tech Point BD</a>.
          </strong>
          All rights reserved.
        </div>
        </>
      ):('')}
    </div>
  );
};

export default App;
