import React, { useEffect, useState, useRef } from "react";
import { FaStream } from "react-icons/fa";
import { FiX } from "react-icons/fi";
import { useSelector } from "react-redux";
import { useNavigate, Link,useLocation } from "react-router-dom";

export const menuItems = [
  {
    name: "Dashboard",
    to: "/dashboard",
    iconClassName: "bi bi-speedometer2",
    roles: ["BROKERS", "BIDDER", "ASSOCIATION", "WAREHOUSE", "TBOARD"],
  },
  {
    name: "Catalogue",
    to: "/catalogue",
    iconClassName: "bi bi-speedometer2",
    roles: ["BROKERS"],
  },
  {
    name: "Auction",
    to: "/auction",
    iconClassName: "bi bi-speedometer2",
    roles: ["BROKERS", "ASSOCIATION", "WAREHOUSE", "TBOARD"],
  },
  {
    name: "Association",
    to: "/association",
    iconClassName: "bi bi-speedometer2",
    roles: ["ASSOCIATION"],
  },
  {
    name: "Bidder",
    to: "/bidders",
    iconClassName: "bi bi-speedometer2",
    roles: ["BIDDER", "ASSOCIATION", "WAREHOUSE", "TBOARD"],
  },
  {
    name: "Reports",
    to: "/reportall",
    iconClassName: "bi bi-speedometer2",
    roles: ["BROKERS","BIDDER", "ASSOCIATION", "WAREHOUSE", "TBOARD"],
  },
];

const SideMenu = (props) => {
  const location = useLocation();
  const history = useNavigate();
  const [inactive, setInactive] = useState(true);
  const [mobile, setMobile] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  
 
  const handleSignout = () => {
    sessionStorage.removeItem('token');
    history('/');
  };

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth < 720) {
        setInactive(true);
        setMobile(true);
      } else {
        setInactive(false);
        setMobile(false);
      }
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  // const sidebarRef = useRef();

  // useEffect(() => {
  //   function handleClickOutside(event) {
  //     if (sidebarRef.current && !sidebarRef.current.contains(event.target)) {
  //     }
  //   }

  //   document.body.addEventListener("click", handleClickOutside);

  //   return () => {
  //     document.body.removeEventListener("click", handleClickOutside);
  //   };
  // }, []);

  // useEffect(() => {
  //   if (inactive) {
  //     removeActiveClassFromSubMenu();
  //   }
  // }, [inactive]);

  const removeActiveClassFromSubMenu = () => {
    document.querySelectorAll(".sub-menu").forEach((el) => {
      el.classList.remove("active");
    });
  };

  const usersString = localStorage.getItem("users");
  const usersd = JSON.parse(usersString);
  const type = usersd.type;
  const filteredMenuItems = menuItems.filter((menuItem) =>
    menuItem.roles.includes(type)
  );

  const handleMenuItemClick = (to) => {
    if (location.pathname === to) {
      return;
    }

    history(to);
  };

  useEffect(() => {
    const handleResize = () => {
      // window.scrollTo(0, 0);
      if (window.innerWidth < 720) {
        setInactive(true);
      } else {
        setInactive(false);
      }
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  useEffect(() => {
    if (inactive) {
      removeActiveClassFromSubMenu();
    }

    props.onCollapse(inactive);
  }, [inactive]);

  return (
    <>
      <div className="topbar">
        <div className={inactive ? "topbar-left lcoll" : "topbar-left"}>
          <div className="">
            {/* <img className="avatar" src={logo} alt="webscript" /> */}
          </div>
          {inactive ? (
            <span className="tlogoh">E</span>
          ) : (
            <span className="tlogoh">E-Auction</span>
          )}
        </div>

        <nav className={inactive ? "navbar-custom rcoll" : "navbar-custom"}>
          <div className="hamburger">
            <FaStream onClick={() => setInactive(!inactive)} />
          </div>
          <div className="top-right">
            <div className="profile">
              <i className="avatar bi bi-person-circle bg-green"></i>
            </div>
            <div className="profile">
              <i className="avatar bi bi-bell bg-blue"></i>
            </div>
            <div className="profile" onClick={handleSignout}>
              <i className="avatar bi bi-box-arrow-left bg-red"></i>
              {isOpen && (
                <div className="dropdown-menu mt-5">
                  <h1>kkkkk</h1>
                  <h1>kkkkk</h1>
                  <button onClick={handleSignout}>Sign out</button>
                </div>
              )}
            </div>
          </div>
        </nav>
      </div>

      <div id="side" className={`side-menu ${inactive ? "inactive" : ""}`}>
   
        <div
          className={` ${mobile && inactive ? "main-menu-m" : "main-menu"}`}
          id="m-menu"
        >  
                {/* {mobile && !inactive &&  (
          <button className="cross-button" onClick={() => setInactive(!inactive)}>
            {inactive ? <FaStream /> : <FiX />}
          </button>
        )} */}

          
          <ul>
            {filteredMenuItems.map((menuItem, index) => {
              const isActive = menuItem.to === location.pathname;

              return (
                <li
                  key={index}
                  className={isActive ? "disabled" : ""}
                  disabled={isActive ? "disabled" : ""}
                >
                  <Link
                    to={menuItem.to}
                    className="menu-item"
                    onClick={() => handleMenuItemClick(menuItem.to)}
                  >
                    <div className="menu-icon">
                      <i className={menuItem.iconClassName}></i>
                    </div>
                    <span>{menuItem.name}</span>
                    <div className="ripple-container"></div>
                  </Link>
                </li>
              );
            })}
          </ul>
        </div>
      
      </div>
    </>
  );
};

export default SideMenu;
