import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  loading: false,
  auction_data: [],
  accounts_data: [],
  catalouge: [],
  status: "idle",
  error: false,
  edit:false,
  refresh:false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;
export const getCatalouge = createAsyncThunk("auc/getcatalouge", async () => {
  // console.log('ksdjkj');
  const response = await axios.get(`${API_URL}/catalouge`);
  return response.data;

});

export const addManualbid = createAsyncThunk("auc/manualbid", async (props) => {
  const response = await axios.put(`${API_URL}/manualbid`, {
   data:props
  });
  return response.data;
});

export const saveData = createAsyncThunk("auc/create", async (accounts) => {
  const response = await axios.post(`${API_URL}/create`, {
    adata: accounts
  });
  return response.data;
});

export const updateData = createAsyncThunk("auc/updatedata", async (data) => {
//  console.log(lcpCart);
  const response = await axios.put(`${API_URL}/updateauction`, {
    adata: data,
  });
  return response.data;
});

export const selectData = createAsyncThunk("auc/selectdata", async (props) => {
  const response= await axios.put(`${API_URL}/selectacccount`,{
    id: props,
  });
    return response.data;
  });
  export const deleteData = createAsyncThunk("auc/deletedata", async (props) => {
 
    const response =axios.put(`${API_URL}/deleteaccount`,{
      id: props,
    })
    return response.data;
      
    });
 
    export const getAuctiondetails = createAsyncThunk("auctiondetails", async () => {
      const response = await axios.get(`${API_URL}/auctiondetails`);
      return response.data;
      
    });
    
const auctionSlice = createSlice({
  name: "auction",
  initialState,
  reducers:{
    increament: (state, action) => {
      const item = state.catalouge.find((item) => item.id === action.payload);
      item.maximum_bid++;
      const response =  axios.put(`${API_URL}/updateauction`, {
        adata: item,
      });
      return response.data;
    },
    dec: (state, action) => {
      const { payload } = action;
      state.catalouge = payload;
    },
  },
 

  extraReducers: {
    [getAuctiondetails.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [getAuctiondetails.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.auction_data = payload;
      state.error = false;
    },
    [getCatalouge.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.catalouge = payload;
      state.error = false;
    },
    [saveData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [saveData.fulfilled]: (state, action) => {
      const { payload } = action;
    },
    [saveData.rejected]: (state) => {
      state.error = true;
    },
    [updateData.pending]: (state, action) => {
      state.loading = true;
      state.error = false;
    },
    [updateData.fulfilled]: (state) => {
      state.edit=false;
    },
    [updateData.rejected]: (state) => {
      state.error = true;
    },
    [selectData.fulfilled]: (state,action) => {
      const { payload } = action;
      state.accounts_data = payload;
      state.edit=true;
    },
    [deleteData.pending]: (state) => {
      state.refresh=false;
    },
    [deleteData.fulfilled]: (state) => {
      state.refresh=true;
    },
  },
});

export const {increament,dec} = auctionSlice.actions;
const auctionReducer = auctionSlice.reducer;

export default auctionReducer;
