import React, { useEffect, useMemo, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import OpenHTMLButton from '../components/reuseable/OpenHTMLButton';
import CatalogueList from "../components/reuseable/CatalougueList";
import AuctionReport from "../components/reuseable/AuctionReport";
import { getAllcatalogue, getAllfactory } from '../store/catalogueSlice';

const ReportAll = () => {
  const dispatch = useDispatch();
  const usersString = localStorage.getItem('users');
  const users = JSON.parse(usersString);
  const [selectedBrokers, setSelectedBrokers] = useState('');
  const [selectedBrokersname, setSelectedBrokersname] = useState('');
  const { factory_list, edit, catalogue_list } = useSelector((state) => state.catalogue);

  const catList = useMemo(
    () => catalogue_list.filter((item) => item.brokers === Number(selectedBrokers))
    .sort((a, b) => a.id - b.id),
    [catalogue_list, selectedBrokers]
  );
  const sortedData = useMemo(
    () => [...catList].sort((a, b) => a.id - b.id),
    [catList]
  );

  const sortedData2 = useMemo(
    () =>
      [...sortedData].sort((a, b) => {
        if (a.factory_name.toLowerCase() < b.factory_name.toLowerCase()) return -1;
        if (a.factory_name.toLowerCase() > b.factory_name.toLowerCase()) return 1;
        return b.id - a.id;
      }),
    [sortedData]
  );

  const sortedData3 = useMemo(
    () =>
      [...catList].sort((a, b) => {
        if (a.factory_name.toLowerCase() < b.factory_name.toLowerCase()) return -1;
        if (a.factory_name.toLowerCase() > b.factory_name.toLowerCase()) return 1;
        return b.id - a.id;
      }),
    [catList]
  );
  const brokers = factory_list.filter((factory) => factory.type === 'BROKERS');
  const handleSelectChange = (e) => {
    setSelectedBrokers(e.target.value)
    const selectedOption = brokers.filter((shead) => shead.id === Number(e.target.value));
    console.log(selectedOption[0].name);
    setSelectedBrokersname(selectedOption[0].name);
  };
  useEffect(() => {
    dispatch(getAllcatalogue());
    dispatch(getAllfactory());
  }, [dispatch]);

  return (
    <>
    <section className="col-lg-12 connectedSortable mt-1">
      <div className="box box-primary">
        <div className="box-header header-custom">
          <div className="row">
            <div className="col-md-6 text-left">
              <h3 className="box-title">Brokers Wise Report</h3>
            </div>
          </div>
        </div>

        <div className="container">
          <div className="row">
            <div className="col-6 col-md-6">
              <select
                required
                value={selectedBrokers}
                onChange={handleSelectChange}
                name="factory"
                autoComplete="off"
                id="factory"
                className="form-control"
              >
                <option value=""> -- Select Brokers -- </option>

                {brokers.map((shead) => (
                  <option key={shead.id} value={shead.id}>
                    {shead.name}
                  </option>
                ))}
              </select>
            </div>
            <div className="col-6 col-md-6">
              <OpenHTMLButton data={catList} user={selectedBrokersname} />
              <CatalogueList data={sortedData3} user={selectedBrokersname} />
                <AuctionReport data={sortedData2} user={selectedBrokersname} /> 
            </div>
          </div>
        </div>
      </div>
    </section>
    
    </>

    
  );
};

export default ReportAll;
