import React from "react";

function generateTable(data,user) {
  console.log(user);
  const total = data.reduce((sum, { totalkg }) => sum + Number(totalkg), 0);
  const nettotal = data.reduce((sum, { gross_total }) => sum + Number(gross_total), 0);
  const html = `
    <table>
      <thead>
        <tr>
        <th>SL</th>
          <th>LOT</th>
          <th>INVOICE</th>
          <th>FACTORY</th>
          <th>Warehouse</th>
          <th>GRADE</th>
          <th>NET.W</th>
          <th>PKG</th>
          <th>GROSS TOTAL</th>
          <th>SAMPLE</th>
          <th>NET TOTAL</th>
          <th>VALUE</th>
          <th>CATEGORY</th>
        </tr>
      </thead>
      <tbody>
        ${data.map(({ lot, invoice, factory_name,warehouse_name,grade,net_weight,pkgs,totalkg,price,category,sample_c,gross_total },index) => `
          <tr>
          <td>${index+1}</td>
            <td>${lot}</td>
            <td>${invoice}</td>
            <td>${factory_name}</td>
            <td>${warehouse_name}</td>
            <td>${grade}</td>
            <td>${net_weight}</td>
            <td>${pkgs}</td>
            <td>${totalkg}</td>
            <td>${sample_c}</td>
            <td>${gross_total}</td>
            <td>${price}</td>
            <td>${category}</td>
          </tr>
        `).join('')}
      </tbody>
      <tfoot>
      <tr>
        <td colspan="8"></td>
        <td>${total} KG</td>
        <td></td>
        <td>${nettotal} KG</td>
        <td></td>
        <td></td>
      </tr>
    </tfoot>
    </table>
  `;

  const newWindow = window.open('');
  newWindow.document.write(`
    <html>
      <head>
        <title>Catalogue List</title>
        <style>
        .table-wrapper {
          margin: 0 auto;
          width: 1100px;
          padding: 20px;
          background-color: #ffffff;
          
      
      }
      body{
        font-family: 'Courier New', Courier, monospace;
        color: #37474F;
  background: #757677;
  font-size: 12px;
      }
          table {
            margin: 0 auto;

            border-collapse: collapse;
           
          }
          th, td {
            padding: 5px;
            border: 1px solid black;
          }
          td {
            white-space: nowrap;
          }
        </style>
      </head>
      <body>
      <div class="table-wrapper">
      <h1 style="text-align:center">${user}</h1>
          <p style="text-align: center;">Season 2022/23</p>
          <hr>
        ${html}
        </div>
      </body>
    </html>
  `);
}

const CatalogueList = ({ data,user }) => {
  const handleClick = () => {
    generateTable(data,user)
  };

  return <button className="btn btn-primary" onClick={handleClick}>List</button>;
};

export default CatalogueList;
