import { createSlice ,createAsyncThunk} from "@reduxjs/toolkit";
import axios from 'axios';
import swal from 'sweetalert';
const initialState = { loading: false, user_data: [],status: 'idle',
error: false,};



const API_URL = process.env.REACT_APP_SERVER_URL;
export const signupUser = createAsyncThunk("signup", async ({loginDetails},reject) => {
  await axios.post(`${API_URL}/signup`,{
      loginDetails: loginDetails,
    }).then((response) => {
    // console.log(response);
    });
    // return response
  });


export const loginUser = createAsyncThunk("login", async ({loginDetails},reject) => {

await axios.post(`${API_URL}/login`,{
    loginDetails: loginDetails,
  }).then((response) => {
    
    if (response.data.message) {
      swal({
        title: "Error!",
        text: response.data.message,
        icon: "warning",
        timer: 2500,
        button: false
      })
      return reject();
    } else {
          swal({
      title: "Done!",
      text: "Your are successfully logged in!!",
      icon: "success",
      timer: 2500,
      button: false
    })
    // return response
    //  console.log(response.data[0]);
    // console.log(response.data);
    localStorage.setItem("users", JSON.stringify(response.data.result[0]));
    sessionStorage.setItem("token",response.data.token);
    sessionStorage.setItem("users", JSON.stringify(response.data.result[0]));
    localStorage.setItem("authKey", JSON.stringify('itp23'));
       return response.data[0];
    }
  });
  // return response
});

export const getuser = createAsyncThunk("getlogin", async () => {
  const response = await axios.get(`${API_URL}/login`)
  return response.data;
});

export const verifyToken = createAsyncThunk("verify", async (token, reject) => {
  try {
    const response = await axios.get(`${API_URL}/verify`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    return response.data;
  } catch (error) {
    console.error('Error verifying token:', error);
    throw error;
  }
});


const loginSlice = createSlice({
  name: "login",
  initialState,
  extraReducers:{
    [loginUser.pending]:(state,action)=>{
        state.loading=true
        state.error=false;
    },
    [loginUser.fulfilled]:(state,action)=>{
      const { payload } = action;
      state.loading=false;
      state.user_data=payload;
      state.status=payload;
      state.error=false;
  },
  [getuser.fulfilled]:(state,action)=>{
    const { payload } = action;
    state.loading=false;
    // state.user_data=payload;
    state.status=payload;
    state.error=false;
},

  [loginUser.rejected]: (state) => {
    state.error = true
  },

}
});

// export const { } = loginSlice.actions;
const loginReducer = loginSlice.reducer;

export default loginReducer;