import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  loading: false,
  catalouge: [],
  account_list: [],
  alldata_list: [],
  alltran_list: [],
  side_list: [],
  lc_list: [],
  lc_product: [],
  lc_sellvehicle:[],
  status: "idle",
  error: false,
};

const API_URL = process.env.REACT_APP_SERVER_URL;

export const getCatalouge = createAsyncThunk("getcatalouge", async () => {
  const response = await axios.get(`${API_URL}/catalouge`);
  return response.data;
});



export const getAccounts = createAsyncThunk("getdata", async () => {
  const response = await axios.get(`${API_URL}/accounts`);
  return response.data;
});

export const getAllsell = createAsyncThunk("getselldata", async () => {
  const response = await axios.get(`${API_URL}/allselldata`);
  return response.data;
});
export const getAlltran = createAsyncThunk("gettrandata", async () => {
  const response = await axios.get(`${API_URL}/accounts`);
  return response.data;
});
export const getAllside = createAsyncThunk("getside", async () => {
  const response = await axios.get(`${API_URL}/sidelist`);
  return response.data;
});
export const getAlllc = createAsyncThunk("getlc", async () => {
  const response = await axios.get(`${API_URL}/lc`);
  return response.data;
});

export const getLcproduct = createAsyncThunk("getlcproduct", async () => {
  const response = await axios.get(`${API_URL}/goods`);
  return response.data;
});

export const getLcsellvehicle= createAsyncThunk("getlcsellv", async () => {
  const response = await axios.get(`${API_URL}/getlcpurv`);
  return response.data;
});



const getDataSlice = createSlice({
  name: "getdata",
  initialState,
  extraReducers: {
    [getCatalouge.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.catalouge = payload;
      state.error = false;
    },
    [getAccounts.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.account_list = payload;
      state.error = false;
    },
    [getAllsell.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.alldata_list = payload;
      state.error = false;
    },
    [getAllside.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.side_list = payload;
      state.error = false;
    },
    [getAlllc.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.lc_list = payload;
      state.error = false;
    },
    [getLcproduct.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.lc_product = payload;
      state.error = false;
    },
    [getLcsellvehicle.fulfilled]: (state, action) => {
      const { payload } = action;
      state.loading = false;
      state.lc_sellvehicle = payload;
      state.error = false;
    },
  },
});

export const {} = getDataSlice.actions;
const getdataReducer = getDataSlice.reducer;

export default getdataReducer;
